/**
 * dialogify
 * https://github.com/porksteak/dialogify
 */
(function($, window, dialogPolyfill){
    'use strict';

    var Dialogify = function(source, options){
        var dialog = window.document.createElement('dialog');
        dialogPolyfill.registerDialog(dialog);

        if (options == null || typeof options != 'object') {
            options = {};
        }

        var content = source.charAt(0) == '#' ? $(source).html() : source;
        content  = '<div class="dialogify__body">' + content + '</div>';

        var widthClass = options.size || 'dialogify__fixedwidth';
        var dialogClass = 'dialogify';
        if (options.fixed !== false) {
            dialogClass += ' fixed';
        }

        var dialogHtml = '<form method="dialog"><div class="dialogify__content ' + widthClass + '"><div></div></div></form>';

        this.id = 'dialogify_' + (++Dialogify.counter);

        $(dialog).attr('id', this.id)
            .addClass(dialogClass)
            .append(dialogHtml).append($closeButton)
            .on('close', function(e){
                $(this).remove();
            })
            .on('cancel', function(e){
                if (options.closable === false) {
                    e.preventDefault();
                }
            })
            .click(function(e){
                if (options.closable !== false && e.target == dialog) {
                    dialog.close();
                }
            })
            .appendTo('body');

        if (options.closable !== false) {
            var $closeButton = $('<a>').addClass('dialogify__close')
                .append('<img src="data:image/svg+xml;base64,">')
                .click(function(e){
                    dialog.close();
                });

            $(dialog).append($closeButton);
        }

        this.$container = $(dialog).find('.dialogify__content > div');
        this.$container.append(content);

        this.showModal = function(){
            dialog.showModal();
        };

        this.show = function(){
            dialog.show();
        };

        this.close = function(){
            dialog.close();
        };
    };

    Dialogify.prototype.title = function(title){
        var $titleBox = $('<h5>').addClass('dialogify_title')
            .append('<img src="data:image/svg+xml;base64,">')
            .append(title);

        if (this.$container.find('.dialogify_title').length) {
            this.$container.find('.dialogify_title').replaceWith($titleBox);
        } else {
            this.$container.prepend($titleBox);
        }

        return this;
    }

    Dialogify.prototype.buttons = function(buttons, options){
        if (!Array.isArray(buttons)) {
            buttons = [];
        }

        if (options == null || typeof options != 'object') {
            options = {};
        }

        var $buttonBox = $('<div>')
            .addClass('btn-box')
            .addClass(options.position || 'text-right');

        var self = this;
        for (var i = 0;i < buttons.length;i++){
            if (typeof buttons[i] == 'string') {
                $buttonBox.append(buttons[i]);
            } else {
                if (buttons[i] == null || typeof buttons[i] != 'object') {
                    buttons[i] = {};
                }

                var $btn = $('<button type="button">')
                    .addClass('btn btn-insert')
                    .addClass(buttons[i].type || '')
                    .data('click', buttons[i].click);

                if (buttons[i].type == Dialogify.BUTTON_PRIMARY) {
                    $btn.attr('type', 'submit');
                }

                if (buttons[i].disabled) {
                    $btn.prop('disabled', true).addClass('is-disabled');
                }

                $btn.text(buttons[i].text || '關閉');
                $btn.click(function(e){
                    if (typeof $(this).data('click') == 'function') {
                        $(this).data('click').call(self, e);
                    }
                });

                $buttonBox.append($btn);
            }
        }

        if (this.$container.find('.btn-box').length) {
            this.$container.find('.btn-box').replaceWith($buttonBox);
        } else {
            this.$container.append($buttonBox);
        }

        return this;
    }

    Dialogify.counter = 0;

    Dialogify.SIZE_LARGE = 'dialogify__autowidth';

    Dialogify.BUTTON_CENTER = 'text-center';
    Dialogify.BUTTON_LEFT = 'text-left';

    Dialogify.BUTTON_PRIMARY = 'btn-primary';
    Dialogify.BUTTON_DANGER = 'btn-danger';

    Dialogify.alert = function(message){
        new Dialogify(message, {closable: false})
            .buttons([{type: Dialogify.BUTTON_PRIMARY}], {position: Dialogify.BUTTON_CENTER})
            .showModal();
    };

    Dialogify.confirm = function(message, ok, cancel){
        new Dialogify(message)
            .buttons([
                {
                    text: '取消',
                    click: function(e){
                        this.close(false);
                        cancel && cancel();
                    }
                },
                {
                    text: '確定',
                    type: Dialogify.BUTTON_PRIMARY,
                    click: function(e){
                        this.close(true);
                        ok && ok();
                    }
                }
            ])
            .showModal();
    };

    // inject css
    if (!$('#dialogifyCss').length) {
        $('<style>')
            .attr({type: 'text/css', id: 'dialogifyCss'})
            .html('.dialogify{display:block;position:absolute;top:50%;bottom:auto;left:50%;right:auto;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);margin:auto;max-width:1100px;min-width:560px;background-color:#fff;font-size:14px;color:#464646;border:none;border-radius:4px;overflow:hidden;box-shadow:0 3px 4px rgba(0,0,0,0.1),0 1px 8px rgba(0,0,0,0.05),0 2px 20px rgba(0,0,0,0.1);z-index:2;padding:0;line-height:100%}.dialogify:not([open]){display:none}.dialogify.fixed{position:fixed;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.dialogify.pixel-style:before{content:"";display:block;position:absolute;top:9px;bottom:9px;left:-9px;right:-9px;background:#FFF;z-index:-1}.dialogify.pixel-style:after{content:"";display:block;position:absolute;top:4px;bottom:4px;left:-5px;right:-5px;background-color:#FFF;background-image:url(../forum/box-corner.png);background-repeat:no-repeat;background-position:-17px 100%;z-index:-1}.dialogify>div{background-repeat:no-repeat;background-position:right bottom}.dialogify>div:after{content:"";position:absolute;bottom:0;left:0;width:20px;height:21px;-webkit-transform:rotate(90deg);transform:rotate(90deg);background-size:20px;background-repeat:no-repeat;opacity:0.7}.dialogify>div hr{border:none;height:1px;background:#E0E6E8}.dialogify .dialogify__content{padding:20px 25px;box-sizing:border-box}.dialogify .dialogify__content-box{margin:20px auto}.dialogify .dialogify__maxheight{max-height:400px;overflow-y:scroll}.dialogify .dialogify__autowidth{max-width:1100px;min-width:560px}.dialogify .dialogify__fixedwidth{max-width:560px}.dialogify .dialogify__close{position:absolute;top:10px;right:10px;color:#00555f}.dialogify .dialogify__close img{width:16px;opacity:0.85}.dialogify .dialogify__close:hover img{opacity:1}.dialogify .btn-danger{box-shadow:0 16px 26px -10px rgba(244,67,54,0.56),0 4px 25px 0px rgba(0,0,0,0.12),0 8px 10px -5px rgba(244,67,54,0.2)}.dialogify .btn-box{margin-top:20px;clear:both}.dialogify .btn-box .btn-insert{margin-left:8px;margin-right:3px}.dialogify .btn-box .check-group input[type="checkbox"]+label h6{font-size:13px;color:#A6A6A6}.dialogify .btn-box .check-group input[type="checkbox"]+label a{color:#117e96}.dialogify .btn-box .check-group input[type="checkbox"]+label a:hover{color:#126e7d;text-decoration:none;transition:all 0.3s}.dialogify .btn-box .float-left a{margin-left:0}.dialogify .btn-box .btn-insert{margin-left:8px;margin-right:3px}.dialogify .link-form__actions{margin-top:10px;float:right;width:100%}.dialogify .btn{display:inline-block;border:1px solid transparent;border-radius:3px;padding:5px 12px;height:auto;font-size:14px;line-height:1.5;letter-spacing:1px;cursor:pointer;background-color:#e5e5e5;color:#A6A6A6}.dialogify .btn:hover{background:#dcdcdc;color:#A6A6A6}.dialogify .btn.is-disabled{opacity:0.5;box-shadow:inset 0 1px 1px rgba(0,0,0,0.2);cursor:not-allowed !important}.dialogify .btn.is-disabled:hover{-webkit-animation:300ms shake;animation:300ms shake}@-webkit-keyframes shake{0%{-webkit-transform:rotate(1.5deg);transform:rotate(1.5deg)}50%{-webkit-transform:rotate(-2deg);transform:rotate(-2deg)}70%{-webkit-transform:rotate(1.5deg);transform:rotate(1.5deg)}100%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}}@keyframes shake{0%{-webkit-transform:rotate(1.5deg);transform:rotate(1.5deg)}50%{-webkit-transform:rotate(-2deg);transform:rotate(-2deg)}70%{-webkit-transform:rotate(1.5deg);transform:rotate(1.5deg)}100%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}}.dialogify .btn.btn-primary{background-color:#117e96;color:#FFF}.dialogify .btn.btn-primary:hover{background-color:#126e7d}.dialogify .btn.btn-danger{background-color:#f44336;color:#FFF}.dialogify .btn.btn-danger:hover{background:#DE2427}.dialogify .btn.btn-danger.is-disabled:hover{background-color:#f44336}.dialogify h5.dialogify_title{font-size:18px;color:#00555f;margin:0 0 15px;font-family:"Helvetica Neue", Helvetica, Roboto, Arial, "Lucida Grande", "PingFang TC", "蘋果儷中黑", "Apple LiGothic Medium","微軟正黑體", sans-serif;font-weight:normal}.dialogify h5.dialogify_title img{width:12px;position:relative;top:-2px;margin-right:3px}.dialogify .text-right{text-align:right}.dialogify .text-center{text-align:center}.dialogify .text-left{text-align:left}.dialogify .text-field{margin:10px 0;border:1px solid #D9D9D9;border-radius:3px;padding:8px;box-sizing:border-box;width:100%;background-color:#FFF;font-size:14px;color:#00555f}.dialogify .text-field:hover,.dialogify .text-field:focus{outline:none;border-color:#117e96}.dialogify .text-field::-webkit-input-placeholder,.dialogify .text-field::-moz-placeholder,.dialogify .text-field::-ms-input-placeholder,.dialogify .text-field::-moz-placeholder{color:#B3B3B3}.dialogify .text-field.is-error{border-color:#DE2427;color:#DE2427}.dialogify textarea.text-field{resize:none;min-height:100px;line-height:1.5;font-size:14px}dialog::-webkit-backdrop{background:#000;opacity:.7}dialog::backdrop{background:#000;opacity:.7}dialog+.backdrop{position:fixed;top:0;left:0;right:0;bottom:0;background:#000;opacity:.7}')
            .appendTo('head')
    }

    window.Dialogify = Dialogify;
})(jQuery, window, dialogPolyfill);
